<template>
    <v-container>
        <v-row class="ma-0 pa-0">
            <v-col align="center">
                <h2>Payment in progress</h2>
                <v-progress-linear color="deep-purple accent-4" indeterminate rounded height="30"></v-progress-linear>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import axios from 'axios'
import common from '@/common'

export default {
    data: () => ({
    }),
    methods: {
    },
    mounted() {
        var vm = this;
        var paymentId = this.$route.query.paymentId;
        var paymentNo = this.$route.query.paymentNo;
        var paymentKey = this.$route.query.paymentKey;
        var amount = this.$route.query.amount;
        var orderId = this.$route.query.orderId;
        var paymentType = this.$route.query.paymentType;

        if(paymentType == "link") {
            axios
            .put(common.makeAPIUrl("/prework/link-payments/request/success"),{
                paymentId : paymentId,
                paymentKey: paymentKey,
            }
            , { headers: { "Content-Type": "application/json", Authorization: this.$session.get('authorization') },})
            .then(() => {
                axios
                .post(common.makeAPIUrl("/prework/link-payments/success"),{
                    amount : amount,
                    paymentId: paymentId,
                    paymentKey: paymentKey,
                    paymentNo: paymentNo,
                }
                , { headers: { "Content-Type": "application/json", Authorization: this.$session.get('authorization') },})
                .then(() => {
                    vm.$alert(common.messages.paymentSuccess, "", "info").then(() => { location.href = "/link-payments?no=" + paymentNo; });
                })
                .catch((err) => {
                    vm.$alert(err.response.data.message, "", "info").then(() => { 
                        var data = new Object();
                        data.errorCode = "";
                        data.errorMessage = err.response.data.message;
                        data.paymentId = paymentId;

                        axios.post(common.makeAPIUrl('/prework/link-payments/fail'),data,
                            { headers: {'Authorization': vm.$session.get('authorization')}}
                        )
                        .then(() => {
                            location.href = "/link-payments?no=" + paymentNo;
                        })
                        .catch((err) => {
                            vm.$alert(err.response.data.message, "", "info").then(() => {
                                location.href = "/link-payments?no=" + paymentNo;
                            });
                        });
                    });
                });
            })
            .catch((err) => {
                vm.$alert(err.response.data.message, "", "info").then(() => { 
                    var data = new Object();
                    data.errorCode = "";
                    data.errorMessage = err.response.data.message;
                    data.paymentId = paymentId;

                    axios.post(common.makeAPIUrl('/prework/link-payments/fail'),data,
                        { headers: {'Authorization': vm.$session.get('authorization')}}
                    )
                    .then(() => {
                        location.href = "/link-payments?no=" + paymentNo;
                    })
                    .catch((err) => {
                        vm.$alert(err.response.data.message, "", "info").then(() => {
                            location.href = "/link-payments?no=" + paymentNo;
                        });
                    });
                });
            });
        } else {
            axios
            .put(common.makeAPIUrl("/payments/card/request/success"), 
            {
                paymentId : paymentId,
                paymentKey: paymentKey,
                amount: amount, 
                orderId: orderId,
                paymentNo: paymentNo
            }
            , { headers: { "Content-Type": "application/json", Authorization: this.$session.get('authorization') },})
            .then(() => {
                vm.$alert(common.messages.paymentSuccess, "", "info").then(() => { location.href="/shipping"; });
            })
            .catch((err) => {
                vm.$alert(err.response.data.message, "", "info").then(() => { 
                    var data = new Object();
                    data.errorCode = "";
                    data.errorMessage = err.response.data.message;
                    data.paymentId = paymentId;

                    axios.put(common.makeAPIUrl('/payments/card/request/fail'),data,
                        { headers: {'Authorization': vm.$session.get('authorization')}}
                    )
                    .then(() => {
                        location.href = "/shipping"; 
                    })
                    .catch((err) => {
                        vm.$alert(err.response.data.message, "", "info").then(() => {
                            location.href = "/shipping"; 
                        });
                    });
                });
            });
        }
    },
}
</script>